import axios from 'axios';
import Router from 'next/router';
import store from '../store';
import { setUserInfo, setLogin, setThemeColor, setLoading } from '../actions/base';
const localURL = 'http://127.0.0.1:3000';
const proURL = '';
const instance = axios.create({
  baseURL: `${localURL}/api`,
  timeout: 5000
});

instance.interceptors.request.use(
  function(config) {
    // store.dispatch(setLoading(true));
    return config;
  },
  function(error) {
    return Promise.reject(error);
  }
);
instance.interceptors.response.use(
  function(response) {
    // http status 200 
    //     
    // .then()

    return response;
  },

  function(error) {
    /*
        http status 200  
          
        .catch()
    */
    if (error.response) {
      if (error.response.status === 401) {
        if (!(error.response.data.message === 'Mismatched pwd')) {
          alert('  .');
        }
      } else if (error.response.status === 400) {
        Router.push('/login');
        store.dispatch(setLogin(false));
        store.dispatch(setUserInfo(undefined));
        store.dispatch(setThemeColor('#7c7cec'));
      } else if (error.response.status === 404) {
        alert(' ');
      } else if (error.response.status >= 500) {
        alert('  . .');
      }
    }
    return Promise.reject(error);
  }
);
export default instance;
